import React from "react";

//components
import { Image } from "react-bootstrap";
//Lazy Loading
import { LazyLoadImage } from 'react-lazy-load-image-component';
import 'react-lazy-load-image-component/src/effects/blur.css'


//MaterialUI
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import Box from '@mui/material/Box';
import Rating from '@mui/material/Rating';
import '../../styles/styles.css'
const useStyles = makeStyles((theme) => ({
  paper: {
  },
  letter: {
  },
  imagenCarousel:{
    maxHeight:400, 
    objectFit:"cover",
  },
  listStyleNone:{
    listStyle:'none'
  },
  
  "@media (min-width: 1200px)": {
    imagen:{
      minWidth: 820,
    },
    imagenCarousel:{
      width:1240,
      height:450,
    },
    row:{
      width:1240,
      maxheight:650,
    },
  },
}));
const changeDuration = (duration) => {
  let tiempo = duration.split(':');
  let new_duration = "";
  if (tiempo) {
    if (tiempo[0] !== "00") {
      new_duration = tiempo[0] + "h";
    }
    if (tiempo[1] !== "00") {
      new_duration = " " + new_duration + " " + tiempo[1] + "min";
    }
    /* if (tiempo[2] !=="00") {
      new_duration = " "+new_duration+" "+tiempo[2]+"s";
    } */
  }
  return new_duration.trim(); // trim() para eliminar cualquier espacio en blanco adicional
};
/**
 * Componente para mostrar en el carousel los videos con algunos campos.
 * @param {*} video
 * @returns Item de un video.
 */
const VideosItem = ({ video,...props } ) => {
  console.log(video);
  const classes = useStyles();
  return (
    <div id="contenedorItemVideo">
      <Button onClick={(e) => props.verVideo(video.id)}>
      <div className={"row  "+classes.row}>
        <div className={"col-md-7  col-12 imgPreview" +classes.imagen}>          
            <LazyLoadImage
              effect="blur"    src={"http://localhost:8000" + video.featured_image}
              className={classes.imagenCarousel }
            ></LazyLoadImage>
        </div>
        <div className={"col-md-5 col-12 " + classes.info}>
          <div>
          <div className="d-flex justify-content-between mt-3 text-capitalize mb-3">
            <p className="text-light h6">{changeDuration(video.duration)}</p>
            <Box sx={{'& > legend': { mt: 2 }}}>
              <div className="bg-secondary d-inline pt-3 px-1 rounded-2">
                <Rating name="read-only" value={parseFloat(video.score)} precision={0.5} readOnly />
              </div>
            </Box>
          </div>
          <h6 className=" text-light text-capitalize text-start border-bottom border-light pb-2">{video.title_espanol}</h6>
          <span className="text-light  text-start text-lowercase truncate-text-6 mb-3">{video.description_esp}</span>
          {video.categorias.length > 0 && (
            <ul className="d-flex flex-wrap justify-content-start p-0 m-0 py-0  text-light">
              <span className="text-capitalize me-1">{video.categorias.length > 1 ? "Categorías" : "Categoría"} → </span>
              {video.categorias.map(categoria => (
                <li key={categoria.id} className="list-group-item list-group-item-light px-2 rounded-4 mb-1"><small className="text-capitalize">{categoria.categoria}</small></li>
              ))}
            </ul>
          )}
          {video.autor.length > 0 && (
            <ul className="d-flex flex-wrap justify-content-start p-0 text-light" >
              
              <span className="text-capitalize">{video.autor.length > 1 ? "Autores" : "Autor" } → </span>
              {video.autor.map(autor => (
                <p key={autor.id} className="px-2 rounded-4 "><small className="text-capitalize mb-2"> {/* • */} {autor.autor}</small></p>
              ))}
            </ul>
          )}
          
          </div>
        </div>
      </div>
      </Button>
    </div>
  );
};

export default VideosItem;
